import React, { ChangeEvent, FC } from 'react'
import { Product } from '../models/Product'

interface EditPostFormProps {
	Modal: boolean
	OnClickCloseModal: () => void
	data: Product
	ApdatePost: (NewPost: Product) => void
	HandleToogleEdit: () => void
}

const initialState = {
	title: '',
	description: '', //  info  description
}

export const EditPostForm: FC<EditPostFormProps> = ({
	HandleToogleEdit,
	OnClickCloseModal,
	ApdatePost,
	Modal,
	data,
}) => {
	//    props
	const [editPost, setEditPost] = React.useState<Product>({
		...initialState,
		...data, //      props
	})

	const handleSubmit: React.FormEventHandler<HTMLFormElement> = (
		e: React.FormEvent<HTMLFormElement>
	) => {
		e.preventDefault()

		const { title, description } = editPost

		// ,    
		if (title && description) {
			ApdatePost(editPost) //  
			HandleToogleEdit() //  
		}
	}

	const HandleChange = (e: ChangeEvent<HTMLInputElement>) => {
		const { name, value } = e.target
		setEditPost({
			...editPost,
			[name]: value, //    editPost
		})
	}

	return (
		<form
			style={{ display: `${Modal ? 'flex' : 'none'}` }}
			className='edit-form'
			onSubmit={handleSubmit}
		>
			<input
				value={editPost.title}
				onChange={HandleChange}
				placeholder=''
				name='title'
			/>
			<input
				value={editPost.description} //  description  info
				onChange={HandleChange}
				placeholder=''
				name='description' //    description
			/>
			<button onClick={OnClickCloseModal}>x</button>
			<button></button>
		</form>
	)
}
